.contacto{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    animation: anim ease-in-out 0.2s;
    .noticias{
        width: 100%;
        height: 50%;    
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;
        .contMedia{
            width: 100%;
            height: 100%;    
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-around;
            .contSvg{
                width: 40%;
                height: 80%;  
                
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: center;
                img{
                    width: 100%;
                    height: 100%;
                }
            }
        }
    }
    .redes{
        width: 100%;
        height: 50%;    
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-evenly;
        .contRedes{
            width: 250px;
            height: 20%;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-evenly;
            .icon{
                width: 10%;
                height: 60%;
                color: rgba(255,222,0,1);
            }
        }
    }
    .footer{
        width: 100%;
        height: 20%;
        background-color: #262626;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        .contOne{
            width: 20%;
            height: 100%;
            img{
                width: 100%;
                height: 100%;
            }
        }
        .contTwo{
            width: 30%;
            height: 100%;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: center;
            font-size: 20px;
            color: white;
        }
        .contThree{
            width: 30%;
            height: 100%;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: center;
            .icon{
                width: 10%;
                height: 10%;
                margin: 0px 10px;
            }
        }
    }
}
@keyframes anim{
    0%{
        clip-path: polygon(0 0, 0 0, 0 100%, 0% 100%);
    }
    100%{
        clip-path: polygon(100% 0, 0 0, 0 100%, 100% 100%);
    }
}