.contStore{
    width: 100%;
    height: 20%;
    margin: 20px 0px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    cursor: pointer;
    .contImgs{
        width: 20%;
        height: 100%;
        border-radius: 50%;
        img{
            width: 100%;
            height: 100%;
        }
    }
    .contDes{
        width: 70%;
        height: 97%;
        background: #FFDE02;
        background-blend-mode: multiply;
        border: 1px solid #D7D7D7;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 0px 45px 45px 0px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
        padding-left: 10px;
        #pname{
            font-style: normal;
            font-weight: 400;
            font-size: 15px;
            line-height: 18px;
            mix-blend-mode: normal;
            border-bottom: 2px solid rgba(0, 0, 0, 0.6);
        }
        #pdesc{
            font-family: 'Roboto Mono';
            font-style: normal;
            font-weight: 400;
            font-size: 14px;
            line-height: 16px;
        }
        b{
            font-size: 12px;
        }
    }
}
.contStore2{
    width: 100%;
    height: 20%;
    margin: 10px 0px;
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: space-evenly;
    cursor: pointer;
    .contImgs{
        width: 20%;
        height: 90%;
        border-radius: 50%;
        background: #D9D9D9;
    }
    .contDes{
        width: 70%;
        height: 97%;
        background: #FFDE02;
        background-blend-mode: multiply;
        border: 1px solid #D7D7D7;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 45px 0px 0px 45px;
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        justify-content: center;
        padding-right: 10px;
        #pname{
            font-style: normal;
            font-weight: 400;
            font-size: 15px;
            line-height: 18px;
            mix-blend-mode: normal;
            border-bottom: 2px solid rgba(0, 0, 0, 0.6);
        }
        #pdesc{
            
            font-style: normal;
            font-weight: 400;
            font-size: 14px;
            line-height: 16px;
        }
        b{
            font-size: 12px;
        }
    }
}