
.productos{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    animation: anim ease-in-out 0.2s;
    .store{
        width: 98%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        .contCatalogo{
            width: 100%;
            height: 15%;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-evenly;
            .contBtnCat{
                width: 15%;
                height: 40%;
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: center;
                img{
                    width: 20%;
                    height: 100%;
                    margin-left: 5px;
                }
            }
            
        }
        .contContents{
            width: 100%;
            height: 80%;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-evenly;
            .contServices{
                width: 35%;
                height: 100%;
                overflow: auto;

            }
            .contServices::-webkit-scrollbar {
                appearance: none;
            }
            .contProduct{
                width: 35%;
                height: 100%;
                overflow: auto;
            }
            .contProduct::-webkit-scrollbar {
                appearance: none;
            }
        }
        
    }
    .contModal{
        width: 90%;
        height: 80%;
        position: absolute;
        background-color: transparent;
        backdrop-filter: blur(10px);
        .contBtnHead{
            width: 100%;
            height: 10%;
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            justify-content: center;
            .icon{
                width: 5%;
                height: 50%;
                border-radius: 50%;
                cursor: pointer;
                color: rgba(255, 0, 0, 0.5);
                transition: color 0.2s;
            }
            .icon:hover{
                color: #D7D7D7;
            }
        }
        .form{
            width: 40%;
            height: 80%;
            display: flex;
            margin: 0 auto;
            flex-direction: column;
            align-items: center;
            justify-content: flex-start;
            padding-top: 20px;
            
            border-radius: 15px;
            background-color: #D9D9D9;
            .contRow{
                width: 100%;
                height: 15%;
                margin: 10px;
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: space-around;
                text-align: center;
                p{
                    font-size: 20px;
                }
                b{
                    font-size: 20px;
                }
                
            }
            .price{
                justify-content: center;
                gap: 5px;
            }
            .rating{
                .contIcons{
                    width: 50%;
                    height: 100%;
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    justify-content: center;
                    .icon{
                        width: 10%;
                        height: 30%;
                    }
                    .iconPerfect{
                        width: 10%;
                        height: 30%;
                        color: rgba(255,222,0,1);
                    }
                }
                .contBtn{
                    width: 50%;
                    height: 100%;
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    justify-content: center;
                    button{
                        width: 40%;
                        height: 40%;
                        border: 1px solid transparent;
                        border-radius: 10px;
                        background-color: rgba(255,222,0,1);
                        transition: all 0.2s;
                        cursor: pointer;
                        text-align: center;
                        display: flex;
                        flex-direction: row;
                        align-items: center;
                        justify-content: space-evenly;
                    }
                    button:hover{
                        background-color: transparent;
                        border: 1px solid rgba(255, 222, 0, 1);
                    }
                }
            }
            .contOptionsForm{
                width: 100%;
                height: 5%;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                margin-bottom: 10px;
                button{
                    width: 20%;
                    height: 100%;
                    cursor: pointer;
                    border-radius: 10px;
                    border: 1px solid transparent;
                    background-color: rgb(255, 0, 0, 0.5);
                    transition: all 0.1s;
                    
                }
                button:hover{
                    width: 25%;
                    background-color: transparent;
                    border: 1px solid black;
                }
            }
        }
    }
    
}
@keyframes anim{
    0%{
        clip-path: polygon(0 0, 0 0, 0 100%, 0% 100%);
    }
    100%{
        clip-path: polygon(100% 0, 0 0, 0 100%, 100% 100%);
    }
}