.contNews{
    width: 100%;
    height: 100%;
    overflow: auto;
    animation: animHome ease-in-out 0.2s;
    .contPanel{
        width: 100%;
        height: 70%;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        .contLeft{
            width: 0%;
            height: 80%;
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            justify-content: center;
            .icon{
                width: 60%;
                height: 60%;
                cursor: pointer;
                transition: all 0.2s;
            }
            .icon:hover{
                width: 80%;
                height: 80%;
                color: yellow;
            }
        }
        .contImg{
            width: 100%;
            height: 95%;
            background-color: beige;
            margin-left: 18px;
            img{
                width: 100%;
                height: 100%;
            }
        }
        .contRigth{
            width: 0%;
            height: 80%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            .icon{
                width: 60%;
                height: 60%;
                cursor: pointer;
                transition: all 0.2s;
            }
            .icon:hover{
                width: 80%;
                height: 80%;
                color: yellow;
            }
        }
    }
    .contStiker{
        width: 100%;
        height: 10%;
        display: flex;
        text-align: center;
        border-bottom: 1px solid black;
    }
    .contPreset{
        width: 100%;
        height: 50%;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-evenly;
        overflow: hidden;
        .contCardPreste{
            width: 20%;
            margin: 0 20px;
            height: 80%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            .contImg{
                width: 100%;
                height: 50%;
                img{
                    width: 100%;
                    height: 100%;
                }
            }
            .contHead{
                width: 100%;
                height: 20%;
                h4{
                    color: #0969da;
                }
                p{
                    color: #555555;
                }
            }
            .contBody{
                width: 100%;
                height: 20%;
                b{
                    color: black;
                }
            }
            .contFooter{
                width: 100%;
                height: 20%;
                p{
                    color: rgb(49, 49, 49);
                }
            }
        }
    }
    .contArti{
        width: 100%;
        height: 70%;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-evenly;
        .contLeft{
            width: 50%;
            height: 100%;
            display: flex;
            flex-wrap: wrap;
            overflow-x: auto;
            .contCardPreste{
                width: 100%;
                margin: 0 20px;
                height: 100%;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                .contImg{
                    width: 100%;
                    height: 60%;
                    img{
                        width: 100%;
                        height: 100%;
                    }
                }
                .contHead{
                    width: 100%;
                    height: 20%;
                    h4{
                        color: #0969da;
                    }
                    p{
                        color: #555555;
                    }
                }
                .contBody{
                    width: 100%;
                    height: 20%;
                    b{
                        color: black;
                    }
                }
                .contFooter{
                    width: 100%;
                    height: 20%;
                    p{
                        color: rgb(49, 49, 49);
                    }
                }
            }
        }
        .contRigth{
            width: 16%;
            height: 90%;
            background-color: rgb(102, 102, 102);
        }
    }
    .contNewsBitcoins{
        width: 100%;
        height: 100%;
        background-color: rgb(59, 59, 59);
    }
}
.contNews::-webkit-scrollbar {
    appearance: none;
}

@keyframes animHome {
    0%{
        clip-path: polygon(0 0, 0 0, 0 100%, 0% 100%);
    }
    100%{
        clip-path: polygon(100% 0, 0 0, 0 100%, 100% 100%);
    }
}